<template>
  <div class="room" v-for="room in rooms" :key="room.info">
    <div class="room-img">
      <img :src="room.img" alt="popular room" />
    </div>
    <div class="room-info">
      <h2 class="room-title">
        {{ room.info }}
      </h2>
      <h3 class="room-price">
        {{ room.price }}
      </h3>
    </div>
  </div>
</template>

<script>
import roomsData from "../roomsData";
export default {
  data() {
    return {
      rooms: roomsData.slice(4, 10),
    };
  },
};
</script>

