<template>
  <header>
    <div class="container">
      <nav class="nav" :class="isOpen ? 'open' : ''">
        <p class="logo">Hotel</p>

        <div class="info">
          <ul class="menu">
            <li v-for="list in lists" :key="list">
              <a :href="`#` + list" class="link" @click="toggleOpen">{{
                list
              }}</a>
            </li>
          </ul>
          
          <label for="checkbox" class="toggler">
            <input type="checkbox" id="checkbox" />
            <span class="ball"></span>
            <i class="fa-solid fa-sun sun"></i>
            <i class="fa-solid fa-moon moon"></i>
          </label>

          <div class="registrate">
            <button class="log-btn">Login</button>
            <button class="sign-btn">Sign Up</button>
          </div>

        </div>

        <div class="burger" @click="toggleOpen">
          <span v-for="span in 3" :key="span"></span>
        </div>
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      lists: ["Home", "Products", "Services", "Blog", "Contact"],
      isOpen: false,
    };
  },

  methods: {
    toggleOpen() {
      if (window.innerHeight <= 768) {
        this.isOpen = !this.isOpen;
      }
    },
  },
};
</script>

