<template>
  <div class="container">
    <section class="luxury">
      <div class="section-title">
        <h2>Choose your Iuxurious room</h2>
      </div>
      <p class="section-subtitle">
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Molestie
      </p>

      <div class="slider-rooms">
        <swiper
          :slidesPerView="1"
          :spaceBetween="10"
          :pagination="{
            clickable: true,
          }"
          :breakpoints="{
            '@0.00': {
              slidesPerView: 1,
              spaceBetween: 10,
            },
            '@0.75': {
              slidesPerView: 2,
              spaceBetween: 20,
            },
            '@1.00': {
              slidesPerView: 3,
              spaceBetween: 40,
            },
            '@1.50': {
              slidesPerView: 4,
              spaceBetween: 50,
            },
          }"
          :modules="modules"
          class="mySwiper"
        >
          <swiper-slide class="swiper-slide" v-for="data in rooms" :key="data.info">
            <img :src="data.img" alt="luxury-room" />
          </swiper-slide>
        </swiper>
      </div>
    </section>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Pagination } from "swiper";
import "swiper/css";
import "swiper/css/pagination";

import roomsData from '../roomsData'

export default {
  data() {
    return {
      modules: [Pagination],
      rooms: roomsData,
    };
  },

  components: {
    Swiper,
    SwiperSlide,
  },
};
</script>

